.secondPage {
  background-image: url('../../vendor//secondPage/background2.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;

  .photoArea {
    position: relative;
    padding: 5vw;
    width: 60vw;
    margin: auto;

    @media screen and (orientation: portrait) {
      width: 100%;
      margin: auto;
      padding: 0;
    }
  }
}
